.live-page {
  padding: 5vw;
  background: linear-gradient(135deg, var(--dl-color-theme-primary2) 10%, var( --dl-color-theme-accent1) 90%);
  height: 100vh;
}

.live-title {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 5vw;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  animation: textGlow 2s ease-in-out infinite alternate;
  text-align: center;
}

.home-button {
  position: absolute;
  top: 2vw;
  right: 5vw;
  padding: 1vw 2vw;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 0.5rem;
  transition: background-color 0.3s, transform 0.3s;
  z-index: 1000;
}

.home-button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.ticket-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2vw;
}

.ticket-card {
  background: white;
  border-radius: 0.625rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  width: calc(100% - 4vw);
  max-width: 300px;
  transition: transform 0.3s, box-shadow 0.3s;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.ticket-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

.ticket-card .ticket-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  display: block;
}

.ticket-info {
  padding: 1.5vw;
  text-align: left;
  flex-grow: 1;
}

.ticket-title {
  font-size: 1.2rem; 
  margin: 0;
  color: #333;
}

.ticket-info p {
  margin: 5px 0;
  color: #555;
}

.status {
  display: flex;
  align-items: center;
  font-weight: bold;
  margin-top: 1rem;
}

.status.active {
  color: green;
}

.green-light {
  width: 10px;
  height: 10px;
  background-color: green;
  border-radius: 50%;
  margin-left: 5px;
  animation: blink-green 1s infinite;
}

.red-light {
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;
  margin-left: 5px;
  animation: blink-red 1s infinite;
}

@keyframes blink-green {
  0%, 100% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
}

@keyframes blink-red {
  0%, 100% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgba(249, 249, 249, 0.8);
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}

.spinner {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.no-tickets-message {
  font-size: 1.5rem;
  color: #333;
  text-align: center;
  margin-top: 20px;
}
@media (min-width: 330px) and (max-width: 426px) {
  .live-title{
    width: 10px;
    margin-left: 40px;
  }
}
.no-tickets-message {
  padding: 20px;
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  margin-top: 20px;
}
